"use client";

import React, { useState } from "react";
import ExamQuestion from "../../../components/ExamQuestion";
import ExamProgressBar from "../../../components/ExamProgressBar";

const ExamPage: React.FC = () => {
  const questions = [
    {
      text: "Lorem ipsum dolor sit amet consectetur.",
      options: [
        { label: "A", value: "Lorem ipsum dolor sit amet consectetur." },
        { label: "B", value: "Vestibulum feugiat est quam pellentesque feugiat." },
        { label: "C", value: "Curabitur pharetra feugiat nec varius." },
        { label: "D", value: "Elit vel mi ut a tortor bibendum viverra quam." },
      ],
      correctAnswer: "A",
    },
    // Agrega más preguntas aquí
  ];

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [answers, setAnswers] = useState<string[]>([]);
  const [showError, setShowError] = useState(false);

  const handleAnswer = (answer: string) => {
    const nextAnswers = [...answers];
    nextAnswers[currentQuestionIndex] = answer;
    setAnswers(nextAnswers);

    if (currentQuestionIndex + 1 < questions.length) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      console.log("Examen finalizado");
    }
  };

  return (
    <div className="p-6 bg-background min-h-screen flex flex-col gap-6">
      <ExamProgressBar
        current={currentQuestionIndex + 1}
        total={questions.length}
      />

      <ExamQuestion
        question={questions[currentQuestionIndex]}
        questionNumber={currentQuestionIndex + 1}
        totalQuestions={questions.length}
        onAnswer={handleAnswer}
      />

      {showError && (
        <p className="text-sm text-red-500">
          Por favor selecciona al menos una respuesta para continuar.
        </p>
      )}

      <div className="flex justify-between mt-4">
        <button
          disabled={currentQuestionIndex === 0}
          onClick={() =>
            setCurrentQuestionIndex((prev) => Math.max(prev - 1, 0))
          }
          className="button--secondary"
        >
          Anterior Pregunta
        </button>
        <button
          onClick={() =>
            answers[currentQuestionIndex]
              ? handleAnswer(answers[currentQuestionIndex])
              : setShowError(true)
          }
          className="button--primary"
        >
          Siguiente
        </button>
      </div>
    </div>
  );
};

export default ExamPage;
