"use client";

import React from "react";

interface ExamQuestionProps {
  question: {
    text: string;
    options: { label: string; value: string }[];
  };
  onAnswer: (answer: string) => void;
  questionNumber: number;
  totalQuestions: number;
}

const ExamQuestion: React.FC<ExamQuestionProps> = ({
  question,
  onAnswer,
  questionNumber,
  totalQuestions,
}) => {
  return (
    <div className="flex flex-col gap-6">
      {/* Encabezado */}
      <div className="flex justify-between items-center">
        <div>
          <h2 className="h24 text-primary-900">Desafío de Conocimiento</h2>
          <p className="p16 text-neutral-600">{question.text}</p>
        </div>
        <p className="text-sm text-neutral-600">
          Pregunta {questionNumber} de {totalQuestions}
        </p>
      </div>

      {/* Barra de progreso */}
      <div className="relative w-full h-2 bg-neutral-200 rounded">
        <div
          className="absolute h-full bg-primary-color rounded"
          style={{ width: `${(questionNumber / totalQuestions) * 100}%` }}
        ></div>
      </div>

      {/* Opciones */}
      <div>
        <h3 className="p16 mb-4 text-primary-900">Elige la respuesta correcta</h3>
        <div className="flex flex-col gap-4">
          {question.options.map((option) => (
            <button
              key={option.value}
              onClick={() => onAnswer(option.value)}
              className="flex items-center gap-4 p-4 border border-neutral-300 rounded-lg hover:bg-primary-light hover:text-primary-900 focus:outline-none"
            >
              <span className="font-bold">{option.label}</span>
              {option.value}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ExamQuestion;
